import React, { Component } from "react";

class Alert extends Component {
  constructor(props) {
    super(props);
    this.color = null;
  }

  getStyle = () => {
    return {
      color: this.color,
    };
  }

  render() {
    return (
      <div className="Alert">
        <p style={this.getStyle()}>{this.props.text}</p>
      </div>
    );
  }
}

class InfoAlert extends Alert {
  constructor(props) {
    super(props);
    this.color = "blue";
  }
}

class ErroAlert extends Alert {
  constructor(props){
    super(props);
    this.color = 'red';
  }
}

class Offline extends Alert {
  constructor(props){
    super(props);
    this.color = 'black'
  }
}

export { InfoAlert, ErroAlert, Offline };
